import React, { useState } from "react"

import BackButton from "./back-button"

const Filters = ({
  location,
  triggerFiltersExpanded,
  title,
  path,
  select,
  children,
}) => {
  const [filtersExpanded, handleFiltersExpanded] = useState(false)

  return (
    <>
      <div className={filtersExpanded ? "back-filter expanded" : "back-filter"}>
        <BackButton location={location} path={path} title={title} />
        {children && (
          <button
            className={filtersExpanded ? "active" : ""}
            onClick={() => {
              const isExpanded = !filtersExpanded
              handleFiltersExpanded(isExpanded)
              if (triggerFiltersExpanded) {
                triggerFiltersExpanded(isExpanded)
              }
            }}
          >
            <span className={filtersExpanded ? "active" : "disabled"}>
              &#x21BF;
            </span>
            <span className={filtersExpanded ? "disabled" : "active"}>
              &#x21C3;
            </span>
            Filtros
          </button>
        )}
        {filtersExpanded ? null : select}
      </div>
      {filtersExpanded && <div className="filter-content">{children}</div>}
    </>
  )
}

export default Filters
